<template>
    <div>
        <h4>Thêm Bài viết</h4>
        <b-row>
            <b-col cols="8">
              <b-row class="mb-2">
                  <b-col cols="2">Tiêu đề</b-col>
                  <b-col><b-input size="sm" v-model="rec.title" ></b-input></b-col>
              </b-row>
                  <vue-ckeditor
                      v-model="rec.content"
                      :config="config"
                      />
            </b-col>
            <b-col>
                <b-button size="sm" @click="on_add()">Add</b-button>
            </b-col>
        </b-row>
    </div>
  </template>
  
  <script>
  import VueCkeditor from '../../components/ComFck.vue';
  import Repository from '../../Repository';
  const filemanager="/js/ckeditor_4.10.0_full/ckeditor/filemanager";
  export default {
    components: { VueCkeditor },
    data() {
      return {
          /* tin_tuc=(id,title,content,hit) */
          rec:{
              title:'Tiêu đề',
              content:'Nội dung',
          },
  
        content: '',
        config: {
          toolbar: [
  
              { name: 'document', items: [ 'Source', '-', 'Save', 'NewPage', 'Preview', 'Print', '-', 'Templates' ] },
              { name: 'editing', items: [ 'Find', 'Replace', '-', 'SelectAll', '-', 'Scayt' ] },
              { name: 'forms', items: [ 'Form', 'Checkbox', 'Radio', 'TextField', 'Textarea', 'Select', 'Button', 'ImageButton', 'HiddenField' ] },
              '/',
              { name: 'basicstyles', items: [ 'Bold', 'Italic', 'Underline', 'Strike', 'Subscript', 'Superscript', '-', 'CopyFormatting', 'RemoveFormat' ] },
              { name: 'paragraph', items: [ 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent', '-', 'Blockquote', 'CreateDiv', '-', 'JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock', '-', 'BidiLtr', 'BidiRtl', 'Language' ] },
              { name: 'links', items: [ 'Link', 'Unlink', 'Anchor' ] },
              { name: 'insert', items: [ 'Image', 'Flash', 'Table', 'HorizontalRule', 'Smiley', 'SpecialChar', 'PageBreak', 'Iframe' ] },
              '/',
              { name: 'styles', items: [ 'Styles', 'Format', 'Font', 'FontSize' ] },
              { name: 'colors', items: [ 'TextColor', 'BGColor' ] },
              { name: 'tools', items: [ 'Maximize', 'ShowBlocks' ] },
              { name: 'about', items: [ 'About' ] }
  
          //   ['Bold', 'Italic', 'Underline', 'Strike', 'Subscript', 'Superscript']
          ],
          height: 300,
          filebrowserBrowseUrl: filemanager + '/browser/default/browser.html?Connector=' + filemanager + '/connectors/php/connector.php',
          filebrowserImageBrowseUrl: filemanager + '/browser/default/browser.html?Type=Image&Connector=' + filemanager + '/connectors/php/connector.php',
          filebrowserFlashBrowseUrl: filemanager + '/browser/default/browser.html?Type=Flash&Connector=' + filemanager + '/connectors/php/connector.php',
          filebrowserUploadUrl: filemanager + '/connectors/php/upload.php?Type=File',
          filebrowserImageUploadUrl: filemanager + '/connectors/php/upload.php?Type=Image',
          filebrowserFlashUploadUrl: filemanager + '/connectors/php/upload.php?Type=Flash',

            //config.removeDialogTabs = 'image:advanced;image:Link;link:advanced;link:upload';   
            //config.filebrowserImageUploadUrl = '/Home/UploadImage' //Action for Uploding image  


        }
      };
    },
    methods: {
          async add(){
              return await Repository.post(`/api/myadmin/tintuc/add`,this.rec).catch(err=>{ console.log(err) });
          },
          on_add(){
              this.add().then(res=>{
                  alert(JSON.stringify(res.data));
              })
          },
    }
  };
  </script>
  